<template>
  <div>
    <transition name="fade">
      <div v-if="show">
        <div class="search-box1">
          <el-input
            v-model="pages.name"
            placeholder="请输入姓名"
            style="width:600px;"
            @input="getList()"
          ></el-input>
          <button class="search-submit-btn2">
            <i class="el-icon-search"></i>搜索
          </button>
        </div>

        <el-card class="box-card" style="width: 100%;margin-top:30px;">
          <el-table style="width: 100%;" :data="lists" height="480">
            <el-table-column prop="loan_name" label="姓名" width="250"></el-table-column>
            <el-table-column prop="loan_card" label="身份证" width="300"></el-table-column>
            <el-table-column prop="loan_card" label="合同状态" width="300">
              <el-tag type="danger">已下载合同</el-tag>
              <!-- <el-button :disabled="true" :type="v.status | FilterType" >{{v.status | FilterName}}</el-button> -->
            </el-table-column>
            <el-table-column prop="address" label="操作">
              <template slot-scope="scope">
                <el-button type="primary" @click="Create(scope.row)">生成合同</el-button>
                <el-button type="success">下载合同</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pages.pageNo"
          :page-sizes="[10, 12, 14, 16]"
          :page-size="pages.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="rows"
          style="margin-top:30px;box-shadow: 0 0 5px 2px #ecf0f1;padding:10px;"
        ></el-pagination>
      </div>
    </transition>
  </div>
</template>

<script>
import { ContractList } from "@/utils/api";

export default {
  data() {
    return {
      lists: [],
      pages: {
        pageNo: 2,
        pageSize: 10,
        name: ""
      },
      pageCount: 0,
      rows: 0,
      centerDialogVisible: false,
      UserInfo: {},
      show: false
    };
  },
  methods: {
    Create(data) {
      console.log(data);
    },
    handleSizeChange(val) {
      this.pages.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.pages.pageNo = val;
      this.getList();
    },
    getList() {
      ContractList(this.pages).then(res => {
        console.log(res);
        if (res.data.code == 20000) {
          let datas = res.data.data.data;
          this.lists = datas.data;
          this.pageCount = datas.pages;
          this.rows = datas.rows;
        } else {
          this.$message.error("请求数据不成功！");
        }
      });
    }
  },
  filters: {
    FilterType(data) {
      switch (data) {
        case 0:
          return "info";
          break;
        case 1:
          return "primary";
          break;
        case 2:
          return "primary";
          break;
        case 3:
          return "warning";
          break;
        case 4:
          return "primary";
          break;
        case 5:
          return "primary";
          break;
        case 6:
          return "warning";
          break;
        case 7:
          return "success";
          break;
      }
    }
  },
  mounted() {
    this.getList();
    this.show = true;
  }
};
</script>
<style scoped>
.search-submit-btn2 {
  width: 150px;
  height: 43px;
  border: none;
  background-color: #0984e3;
  color: white;
  outline: none;
}
.fade-enter-active {
  transition: 1s;
  opacity: 1;
  margin-left: 0px;
}
.fade-enter {
  opacity: 0;
  margin-left: 100px;
}
</style>